import React, { Component} from 'react'
import ReactDOM from 'react-dom'
import './index.css'
import * as serviceWorker from './serviceWorker'
import English from './English'
import Arabic from './Arabic'
import {Added} from './AddedData'
import AddedFruits from './AddedData'
import detectPhone from './detectPhone'
import * as firebase from "firebase/app";
import "firebase/analytics";
import "firebase/auth";
import "firebase/firestore";
import "firebase/database";
import Secret from './SecretMenu'
//import data from './data.json'

const firebaseConfig = {
    apiKey: "AIzaSyAgZxw_Wz_Wzdic0YAP20BPA9KMU6QlRtE",
    authDomain: "fruitsindex.firebaseapp.com",
    databaseURL: "https://fruitsindex.firebaseio.com",
    projectId: "fruitsindex",
    storageBucket: "fruitsindex.appspot.com",
    messagingSenderId: "849400942163",
    appId: "1:849400942163:web:fd8767e79f4f56099440a6",
    measurementId: "G-ZRV5X48M3Q"
  };
firebase.initializeApp(firebaseConfig);
let db = firebase.firestore();
let data= [];
let isAr = false

var updateHandle = function () {this.setState({fruitAdded: this.mapAdded(Added)}); this.setState({sum: this.sumHandel()})}
var updateState = function () {this.setState({fruitData: this.mapData(data), ready:true})}

class App extends Component {

    constructor() {
        super()
        this.state = {
            ar:false, 
            isPhone: detectPhone(), 
            fruitData:'', 
            fruitAdded:'', 
            sum: '', 
            ready: false,
        }
        updateHandle=updateHandle.bind(this)
        updateState=updateState.bind(this)
    }
    updateState = () => this.setState({fruitData: this.mapData(data), ready:true})
    componentDidMount() {
        if (this.state.isPhone) {
            document.getElementById('diaryContainerPc').id= 'DiaryContainerPhone';
            document.getElementById('sumPc').id= 'sumPhone';
        };  
        db.collection("App").get().then(function(querySnapshot) {
            return new Promise(resolve => {
            resolve(querySnapshot.forEach(function(doc) {
               data.push(doc.data())
        }))}).then(() => {data.sort((a, b) => (a.id > b.id) ? 1 : -1); updateState()})
    })
        
            // for (let e of data){
            //     db.collection("App").doc(e.id.toString()).set(e)
            //     console.log(e.id)
            // }
            //var docRef = db.collection("App")

            // docRef.get().then(function(doc) {
            //     if (doc.exists) {
            //         console.log("Document data:", doc.data());
            //     }
            //         })
            
        //})
    }
    isReady = () => {
        if (!this.state.ready) {
            return <h1>Loading...</h1>
        } else {
            return this.state.fruitData
        }
    }
    mapData(arr) {
        return arr.map((el, index) => 
        <English 
        key={index} 
        id={el.id} 
        img={el.img} 
        name={el.name[0]} 
        cal={el.cal} 
        fat={el.fat} 
        carb={el.carb} 
        fiber={el.fiber} 
        sugar={el.sugar} 
        protein={el.protein} 
        serving={el.serving} 
        />);
    }
    mapDataAR(arr) {
        return arr.map((el, index) => 
        <Arabic key={index} 
        id={el.id} 
        img={el.img} 
        name={el.name[1]} 
        cal={el.cal} 
        fat={el.fat} 
        carb={el.carb} 
        fiber={el.fiber} 
        sugar={el.sugar} 
        protein={el.protein} 
        serving={el.serving}
        />);
    }

    mapAdded(arr) {return arr.map((el, index) => 
    <AddedFruits 
    key={index} 
    id={el.id} 
    gram={el.gram} 
    name={el.name} 
    cal={((el.cal * el.gram) / 100).toFixed(0)} 
    fat={el.fat} 
    carb={el.carb} 
    fiber={el.fiber} 
    sugar={el.sugar} 
    protein={el.protein} /> )}

    sumHandel =  () => {
        let x = Added.map(o => Number(((o.cal * o.gram) / 100).toFixed(0)));
        if (Added.length === 0) { // Do nothing
        } else if (this.state.ar) {
            x = x.reduce((total, num) => total + num); return 'المجموع ' + x + ' سعرة'
        } else {
            x = x.reduce((total, num) => total + num); return 'Total ' + x + ' kcal'}   
    }

    Lang = () => {
        if (this.state.ar) {
            isAr = false;
            Added.splice(0, Added.length);
            this.setState({fruitData: this.mapData(data), ar: false, fruitAdded:'', sum: ''});
            if (this.state.isPhone) {document.getElementById('sumPhone').style.display='none'} else {document.getElementById('sumPc').style.display = 'none';}
            document.getElementById('cardsContainer').style.marginLeft='0px'
            document.getElementById('lang').innerHTML='العربية'
            document.getElementById('searchInput').value = ""
            document.getElementById('choose').innerHTML='Sort By'
            document.getElementById('choose').selected='defaultvalue'
            document.getElementById('AlphaSort').innerHTML='Alphabetical'
            document.getElementById('CalSort').innerHTML='Kcal'
            document.getElementById('SugarSort').innerHTML='Sugar'
            document.getElementById('CarbsSort').innerHTML='Carbs'
            document.getElementById('FiberSort').innerHTML='Fiber'
            document.getElementById('ProteinSort').innerHTML='Protein'
            document.getElementById("searchInput").placeholder = 'Search'
            document.getElementById('root').style.direction='ltr'
        } else {
            isAr = true;
            Added.splice(0, Added.length);
            this.setState({fruitData: this.mapDataAR(data), ar: true, fruitAdded:'', sum: ''});
            if (this.state.isPhone) {document.getElementById('sumPhone').style.display='none'} else {document.getElementById('sumPc').style.display = 'none';}
            document.getElementById('cardsContainer').style.marginLeft='0px'
            document.getElementById('lang').innerHTML='English'
            document.getElementById('searchInput').value = "";
            document.getElementById('choose').selected= 'defaultvalue'
            document.getElementById('choose').innerHTML='ترتيب حسب'
            document.getElementById('AlphaSort').innerHTML='الأبجدية'
            document.getElementById('CalSort').innerHTML='الطاقة'
            document.getElementById('SugarSort').innerHTML='السكر'
            document.getElementById('CarbsSort').innerHTML='السكريات'
            document.getElementById('FiberSort').innerHTML='الألياف'
            document.getElementById('ProteinSort').innerHTML='البروتين'
            document.getElementById("searchInput").placeholder = 'بحث'
            document.getElementById('root').style.direction='rtl'
        }
    }

    changeHandle = () => {
        let sort = document.getElementById("sort").value;
        if (sort === "AlphaSort") {
            if (this.state.ar) {let AlphaSorted = data.sort((a, b) => (a.name[1] > b.name[1]) ? 1 : -1); this.setState({fruitData: this.mapDataAR(AlphaSorted)})
            } else {let AlphaSorted = data.sort((a, b) => (a.name[0] > b.name[0]) ? 1 : -1); this.setState({fruitData: this.mapData(AlphaSorted)})}
        } else if (sort === "CalSort") {
            let CalSorted = data.sort((a, b) => (a.cal < b.cal) ? 1 : -1);
            let calMap = this.state.ar? this.mapDataAR(CalSorted): this.mapData(CalSorted);
            this.setState({fruitData: calMap});
        } else if (sort === "SugarSort") {
            let SugarSorted = data.sort((a, b) => (a.sugar < b.sugar) ? 1 : -1);
            let SugarMap = this.state.ar? this.mapDataAR(SugarSorted): this.mapData(SugarSorted);
            this.setState({fruitData: SugarMap});
        } else if (sort === "CarbsSort") {
            let CarbsSorted = data.sort((a, b) => (a.carb < b.carb) ? 1 : -1);
            let CarbMap = this.state.ar? this.mapDataAR(CarbsSorted): this.mapData(CarbsSorted);
            this.setState({fruitData: CarbMap});
        } else if (sort === "FiberSort") {
            let FiberSorted = data.sort((a, b) => (a.fiber < b.fiber) ? 1 : -1);
            let FiberMap = this.state.ar? this.mapDataAR(FiberSorted): this.mapData(FiberSorted);
            this.setState({fruitData: FiberMap})
        } else if (sort === "ProteinSort") {
            let ProteinSort = data.sort((a, b) => (a.protein < b.protein) ? 1 : -1);
            let ProteinMap = this.state.ar? this.mapDataAR(ProteinSort): this.mapData(ProteinSort);
            this.setState({fruitData: ProteinMap})
        }
    }

    search = (event) => {
        let srchInput = event.target.value
        if (srchInput.length > 1) {
            let y = [], flat = []
            let max = data.map(e => e.name.length).reduce((a,b) => Math.max(a,b))
            for (let i=0; i < max; i++){
                let f = data.filter(o => o.name[i])
                if (f.length !== 0) {y.push(f.filter(o => o.name[i].toLowerCase().indexOf(srchInput.toLowerCase()) !== -1))}
            }
            flat = flat.concat(...y)
            let srch = this.state.ar? this.mapDataAR(flat): this.mapData(flat);
            this.setState({fruitData: srch});
            if (srchInput === '134625') {this.setState({fruitData: <Secret />})}
        } else {
            let srch = this.state.ar? this.mapDataAR(data): this.mapData(data); this.setState({fruitData: srch})
        }
    }

    render() {
        return (
            <div>
                <header id='navbar' className='srchConrainer'>
                    <input id='searchInput' type='text' placeholder="search" onChange={this.search} autoComplete="nope"></input><br />
                    <select id="sort" defaultValue={'DEFAULT'} onChange={this.changeHandle}>
                    <option id="choose" value="DEFAULT" disabled>Sort By</option>
                    <option id="AlphaSort" value="AlphaSort" onClick={this.AlphaSort}>Alphabetical</option>
                    <option id="CalSort" value="CalSort">Kcal</option>
                    <option id="SugarSort" value="SugarSort">Sugar</option>
                    <option id="CarbsSort" value="CarbsSort">Carbs</option>
                    <option id="FiberSort" value="FiberSort">Fiber</option>
                    <option id="ProteinSort" value="ProteinSort">Protein</option>
                    </select>
                    <button id='lang' onClick={this.Lang}>العربية</button>
                </header>

                <div id='diaryContainerPc'>
                    {this.state.fruitAdded} <br />
                    <div id='sumPc'>
                        <p>{this.state.sum}</p>
                    </div>
                </div>

                <div id='cardsContainer'>
                    {this.isReady()}
                </div>

            </div>
        )
    }
}

ReactDOM.render(<App />, document.getElementById('root'))
serviceWorker.unregister();

export default updateHandle
export {isAr}
export {data}
export {db}